<template>
  <div class="search-box-wrapper">
    <i class="iconfont icon-search"></i>
    <div class="mine-search-box" v-if="findtxt">{{placeholder}}</div>
    <input
      class="mine-search-box"
      type="text"
      title="搜索"
      :placeholder="placeholder"
      v-model="query"
      v-if="!findtxt"
      ref="input"
    >
    <i class="iconfont icon-close"  v-show="query" @click="reset"></i>
  </div>
</template>

<script>
//引用防抖函数
import {debounce} from 'commonjs/utils';
export default {
    name: 'SearchBox',
    props: {
      placeholder: {
        type: String,
        default: '搜索商品/店铺/品牌'
      },
      findtxt: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        query: ''
      };
    },
    watch: {
      //实时监听当前输入的内容，并且把值传给父组件
      query: debounce(function () {
        this.$emit('query', this.query);
      },50)
    },
    methods: {
      focus() {
        this.$refs.input && this.$refs.input.focus();
      },
      //处理清空input的内容
      clear() {
        this.query = '';
      },
      //点击清空按钮
      reset() {
        this.clear();
        this.focus();
      }
    }
  
};
</script>

<style lang="scss" scoped>
  .search-box-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    height:30px;
    padding: 0 7px;
    background-color: #fff;
    border-radius:30px / 2;
  }

  .iconfont {
    color: #ccc;
    font-size: 18px;
    font-weight: bold;
  }

  .mine-search-box {
    flex: 1;
    background: none;
    border: none;
    margin: 0 6px;
    color: #666;
    line-height: 1.5;
  }
</style>
