<template>
  <div class="my-home">
    <!--头部搜索-->
    <header class="header-g">
        <home-header :class="{'header-transition': isHeaderTransition}" ref="header"></home-header>
    </header>
     <!--滚动条-->
     <my-scroll
      pullDown
      pullUp
      @pull-down="pullToRefresh"
      @pull-up="pullToLoadMore"
      @scroll-end="scrollEnd"
      @scroll="scroll"
      @pull-down-transition-end="pullDownTransitionEnd"
      ref="scroll"
     >
        <!--幻灯片-->
        <home-swiper :bannerlist="banners"></home-swiper>
          <!--菜单列表-->
        <home-nav :recommends="recommends"></home-nav>
        <!--本周流行-->
        <home-pop></home-pop>
        <!--本周流行-->
        <tab-menu :tabmenulist="tabControlTitles" @tabClick="tabClick"></tab-menu>
         <!--商品列表-->
         <goods-list :goods="showGoods"></goods-list>
     </my-scroll>
      <!--返回顶部-->
     <div class="backtop">
        <back-top :visible="isBacktopVisible" @backtop="backToTop"></back-top>
    </div>   
  </div>
</template>

<script>
//内部引用
import HomeHeader from "views/home/modular/HomeHeader";
import HomeSwiper from "views/home/modular/HomeSwiper";
import HomeNav from "views/home/modular/HomeNav";
import HomePop from "views/home/modular/HomePop";

//外部组件
import TabMenu from "components/common/tabmenu/TabMenu";
import MyScroll from "components/common/scroll/MyScroll";
import GoodsList from "components/content/goods/GoodsList";
import BackTop from "components/common/backtop/BackTop";

import { getHomeSwiperData, getHomeGoods } from "@/network/home";
export default {
  name: "MyHome",
  data(){
     return {
         banners:[],
         recommends:[],
         tabControlTitles: ["流行", "新款", "精选"],
         goods: {
          'pop': {page: 0, list: []},
          'new': {page: 0, list: []},
          'sell': {page: 0, list: []},
         },
         currentType:'pop',
         //返回顶部
         isBacktopVisible: false,
         //头部是否隐藏
         isHeaderTransition: false
     }
  },
  components:{
      HomeHeader,
      HomeSwiper,
      HomeNav,
      HomePop,
      TabMenu,
      MyScroll,
      GoodsList,
      BackTop
  },
   computed: {
      showGoods() {
        return this.goods[this.currentType].list
      }
  },
  created() {
      this.getHomeSwiperList();
       // 2.请求商品数据
      this.getHomeGoodList('pop')
      this.getHomeGoodList('new')
      this.getHomeGoodList('sell')
  },
  methods: {

    //数据请求操作-----------------------------------------------------------------------------------------------------------------------------
      getHomeSwiperList(){
           getHomeSwiperData().then(res => {
              //console.log(res);
              //幻灯片数据
              this.banners = res.data.banner.list;
              //菜单数据
              this.recommends = res.data.recommend.list;
           });
      },
      getHomeGoodList(type) {
          const page = this.goods[type].page + 1;
          getHomeGoods(type, page).then(res =>{
                console.log(res);
                let _this=this
                _this.goods[type].list.push(...res.data.list);
                _this.goods[type].page += 1;
          });
      },
    //结束数据请求操作----------------------------------------------------------------------------------------------------------------------------- 
    
    //DOM操作-----------------------------------------------------------------------------------------------------------------------------
      tabClick(index) {
          if (index === 0) {
            this.currentType = "pop";
          } else if (index === 1) {
            this.currentType = "new";
          } else if (index === 2) {
            this.currentType = "sell";
          }
      },
      pullToRefresh(end) {
        //this.$refs.slider.update().then(end);
        // setTimeout(() =>{
        //   console.log('下拉刷新');
        //   end();
        // },1000);
        end();
      },
      //触发到底部加载更多页面
      pullToLoadMore(end) {
         this.getHomeGoodList(this.currentType)
         end();
      },
      scroll(translate) {
        this.changeHeaderStatus(translate);
      },
      //手指触发离开后
      scrollEnd(translate, scroll, pulling) {
        if (!pulling) {
          this.changeHeaderStatus(translate);
        }
        this.isBacktopVisible = translate < 0 && -translate > scroll.height;
      },
      //点击按钮返回顶部
      backToTop() {
        this.$refs.scroll && this.$refs.scroll.scrollToTop();
      },
      pullDownTransitionEnd() {
       // this.$refs.header.show();
      },
      changeHeaderStatus(translate) {
        //如果我小于0就隐藏否则显示
        // if (translate > 0) {
        //   this.$refs.header.hide();
        //   return;
        // }
        // this.$refs.header.show();
       //如果滑动正数大于100话显示背景
        this.isHeaderTransition = -translate > 100;
      }
  }

};
</script>

<style lang="scss" scoped>
.my-home{
  height: calc(100vh -50px);
  .header-g{
      position: absolute;
      left: 0;
      top: 0;
      z-index: 9;
      width: 100%;
  }
  .backtop{
    position: absolute;
    z-index: 1100;
    right: 10px;
    bottom: 60px;
  }
}
</style>